
svg{width:100%;height:100%;top:0px;left:0px;z-index:0
}
.input{position:absolute;z-index:1;font-size:20px;text-align:center;left:50%;-ms-transform:translateX(-50%);transform:translateX(-50%);font-family:helvetica, sans-serif;bottom:20px;background:none;border:1px solid #ddd;color:#eee
}
.text,.offscreen-text{font-family:'Rubik Mono One', sans-serif !important;width:100%;top:50%;-ms-transform:translateY(-50%);transform:translateY(-50%);display:block;position:absolute;margin:0
}
.text span,.offscreen-text span{font-size:50px
}
.offscreen-text{text-align:center;top:-9999px
}
.text span{position:absolute
}



