
.loading-container{background-color:#2e3141;height:100vh;overflow:hidden
}
.fade-enter-active,.fade-leave-active{transition:opacity .8s
}
.fade-enter,.fade-leave-to{opacity:0
}
.contact-form-container{background-color:#222;margin:0 auto;width:100%;min-height:700px;margin:0 auto;border-radius:8px;position:relative;overflow:hidden;box-shadow:0px 9px 13px 0px rgba(0,0,0,0.3);-webkit-transform:scale(1)
}
.contact-form-container .menu-trigger,.contact-form-container .close-trigger{position:absolute;top:32px;right:20px;display:block;width:42px;height:42px;cursor:pointer;z-index:333
}
.contact-form-container .menu-trigger:hover .menu-trigger-bar:before,.contact-form-container .close-trigger:hover .menu-trigger-bar:before{width:100%
}
.contact-form-container .menu-trigger:hover .close-trigger-bar:before,.contact-form-container .close-trigger:hover .close-trigger-bar:before{width:100%
}
.contact-form-container .close-trigger{z-index:5;top:42px
}
.contact-form-container .menu-trigger-bar{display:block;width:100%;height:4px;background-color:#fff;margin-bottom:6px;-ms-transform:rotate(-45deg);transform:rotate(-45deg);position:relative
}
.contact-form-container .menu-trigger-bar:before{content:"";position:absolute;top:0;left:0;display:block;width:0%;height:100%;background-color:rgba(0,0,0,0.2);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1)
}
.contact-form-container .menu-trigger-bar.top{width:50%
}
.contact-form-container .menu-trigger-bar.middle:before{left:auto;right:0
}
.contact-form-container .menu-trigger-bar.bottom{width:50%;margin-left:50%
}
.contact-form-container .close-trigger-bar{display:block;width:100%;height:4px;background-color:#222;position:relative
}
.contact-form-container .close-trigger-bar:before{content:"";position:absolute;top:0;left:0;display:block;width:0%;height:100%;background-color:rgba(255,255,255,0.2);transition:all 0.3s cubic-bezier(0.55, 0, 0.1, 1)
}
.contact-form-container .close-trigger-bar.left{-ms-transform:translateX(100px) translateY(-100px) rotate(-45deg);transform:translateX(100px) translateY(-100px) rotate(-45deg)
}
.contact-form-container .close-trigger-bar.right{-ms-transform:translateX(-100px) translateY(-100px) rotate(45deg);transform:translateX(-100px) translateY(-100px) rotate(45deg);top:-3px
}
.contact-form-container .logo{display:block;width:64px;height:64px;position:absolute;top:50%;left:50%;margin-top:-52px;margin-left:-32px;text-align:center;z-index:10
}
.contact-form-container .logo span{display:block;text-align:center;line-height:62px;font-size:72px;color:#fff
}
.contact-form-container .logo-title{position:absolute;top:10px;left:0;width:100%;height:100%;color:#222;font-size:32px;line-height:82px
}
.contact-form-container .logo-badge{position:absolute;display:block;width:24px;height:24px;top:6px;right:-6px;border-radius:100%;background-color:#6295ca;font-size:12px;line-height:2;text-align:center
}
.contact-form-container .inner-container,.contact-form-container .menu-container{position:absolute;height:100%;width:100%;left:0;top:0
}
.contact-form-container .inner-container{z-index:20
}
.contact-form-container .menu-container{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.contact-form-container .menu{display:block;width:100%
}
.contact-form-container .menu-bg{position:absolute;display:block;width:200%;left:-53%;top:-25%;height:75%;background-color:rgba(0,0,0,0.1)
}
.contact-form-container .menu-bg.middle{top:25%;left:-50%;-ms-transform:rotate(-45deg) scaleY(0);transform:rotate(-45deg) scaleY(0)
}
.contact-form-container .menu-bg.top{left:-50%;top:-5%;-ms-transform:rotate(-45deg) translateY(-156%);transform:rotate(-45deg) translateY(-156%)
}
.contact-form-container .menu-bg.bottom{top:100%;-ms-transform:rotate(-45deg) translateY(35%);transform:rotate(-45deg) translateY(35%);left:-5%
}
.contact-form-container .valid-err-msg{display:none
}
.contact-form-container .error input,.contact-form-container .error textarea{border-color:#f60
}
.contact-form-container .error .valid-err-msg{display:block
}
textarea{resize:none
}



