a{transition:color .2s ease-in-out, border-bottom-color .2s ease-in-out;border-bottom:dotted 1px rgba(255,255,255,0.35);color:#fff;text-decoration:none
}
a:hover{border-bottom-color:transparent;color:#fff !important
}
a.special:not(.button){text-decoration:none;border-bottom:0;display:block;font-family:Raleway,Helvetica,sans-serif;font-size:0.8em;font-weight:700;letter-spacing:.1em;margin:0 0 2em 0;text-transform:uppercase
}
a.special:not(.button):before{-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-family:Font Awesome\ 5 Free;font-style:normal;font-weight:900;text-transform:none !important
}
a.special:not(.button):before{transition:background-color .2s ease-in-out;border-radius:100%;border:solid 2px rgba(255,255,255,0.125);content:'\f105';display:inline-block;font-size:1.25em;height:2em;line-height:1.65em;margin-right:0.85em;text-align:center;text-indent:0.15em;vertical-align:middle;width:2em
}
a.special:not(.button):hover:before{background-color:rgba(255,255,255,0.025)
}
a.special:not(.button):active:before{background-color:rgba(255,255,255,0.075)
}
