input[type="checkbox"]+label,input[type="radio"]+label{text-decoration:none;color:#fff;cursor:pointer;display:inline-block;font-size:1em;font-family:"Source Sans Pro",Helvetica,sans-serif;text-transform:none;letter-spacing:0;font-weight:300;padding-left:2.4em;padding-right:0.75em;position:relative
