.features{display:-moz-flex;display:-ms-flex;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:0 0 2em 0
}
.features article{padding:1.75em 1.75em .1em 1.75em ;background-color:#353849;border-radius:5px;margin:1.5em 3em 1.5em 0;width:calc(50% - 1.5em)
}
.features article:nth-child(2n){margin-right:0
}
.features article .image{border-radius:5px 5px 0 0;display:block;margin-bottom:1.75em;margin-left:-1.75em;margin-top:-1.75em;position:relative;width:calc(100% + 3.5em)
}
