.blog-card-wrapper{position:relative;height:400px
}
.blog-card{width:100%;height:400px;position:absolute;overflow:hidden;border-radius:10px;box-shadow:3px 3px 20px rgba(0,0,0,0.5);text-align:center
}
.blog-card.spring-fever{background:url("~@/assets/images/bg/paralax-blue-800.jpg") no-repeat
}
.blog-card .color-overlay{background:rgba(84,104,110,0.4);width:100%;height:400px;position:absolute;z-index:10;top:0;left:0;transition:background 0.3s cubic-bezier(0.33, 0.66, 0.66, 1)
}
.blog-card .gradient-overlay{background-image:linear-gradient(transparent 0%, rgba(0,0,0,0.6) 21%);width:100%;height:400px;position:absolute;top:350px;left:0;z-index:15
}
.blog-card:hover .card-info{opacity:0;bottom:0
}
.blog-card:hover .card-overlay-text{opacity:1
}
.blog-card:hover .color-overlay{background:rgba(84,104,110,0.8)
}
.blog-card h3{font-size:20px;font-weight:500;letter-spacing:2px;color:#9CC9E3;font-family:'Roboto', sans-serif;margin-bottom:0
}
.blog-card hr{width:50px;height:3px;margin:20px auto;border:0;background:#D0BB57
}
.title-content{text-align:center;margin:30px 0 0 0;position:absolute;z-index:20;width:100%;top:0;left:0
}
.title-content .section-icon{font-size:40px;font-size:2.5rem;line-height:40px;line-height:2.5rem;margin-bottom:30px
}
.intro{width:170px;margin:0 auto;color:#DCE3E7;font-family:'Droid Serif', serif;font-size:13px;font-style:italic;line-height:18px
}
.card-info,.card-overlay-text{width:100%;position:absolute;left:0;margin:0 auto;padding:0 50px;color:#DCE3E7;font-family:'Droid Serif', serif;line-height:24px;z-index:20;bottom:0;opacity:1;transition:bottom 0.3s,opacity 0.3s cubic-bezier(0.33, 0.66, 0.66, 1);height:240px
}
.card-info{opacity:1
}
.card-overlay-text{opacity:0;bottom:0
}
.utility-info{position:absolute;bottom:0px;left:0;z-index:20
}
.utility-list{list-style-type:none;margin:0 0 30px 20px;padding:0;width:100%
}
.utility-list li{margin:0 15px 0 0;padding:0 0 0 22px;display:inline-block;color:#DCE3E7;font-family:'Roboto', sans-serif
}
.utility-list li.comments{background:url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/1765/icon-chat.svg) no-repeat 0 0.1em
}
.utility-list li.date{background:url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/1765/icon-calendar.svg) no-repeat 0 0.1em
}
#header{transition:background-color .2s ease-in-out;background-color:rgba(53,56,73,0.95);height:3.5em;left:0;line-height:3.5em;padding:0 1.25em;position:fixed;top:0;width:100%;z-index:10000
}
#header h1{transition:opacity .2s ease-in-out,visibility .2s;border-bottom:0;font-size:0.8em;margin-bottom:0;opacity:1;visibility:visible
}
#header h1 a{border:0
}
#header nav{font-family:Raleway,Helvetica,sans-serif;font-size:0.8em;font-weight:700;height:3em;letter-spacing:.1em;line-height:3em;position:absolute;right:0.7em;text-transform:uppercase;top:0.7em
}
#header nav a{border:0;display:inline-block;padding:0 1em
}
#header nav a:before{float:right;margin-left:0.75em
}
#header nav a[href="#menu"]{text-decoration:none;transition:background-color .2s ease-in-out;border-radius:5px;box-shadow:inset 0 0 0 2px rgba(255,255,255,0.125);padding:0 1.35em
}
#header nav a[href="#menu"]:before{-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;font-family:Font Awesome\ 5 Free;font-style:normal;font-weight:900;text-transform:none !important
}
#header nav a[href="#menu"]:before{content:'\f0c9'
}
#header nav a[href="#menu"]:hover{background-color:rgba(255,255,255,0.025)
}
#header nav a[href="#menu"]:active{background-color:rgba(255,255,255,0.075)
}
#header.alt{background-color:transparent
}
#header.alt h1{opacity:0;visibility:hidden
}
@media screen and (max-width: 736px){
#header{height:2.75em;line-height:2.75em
}
#header nav{top:0;right:0;height:inherit;line-height:inherit
}
#header nav a{height:inherit;line-height:inherit
}
#header nav a[href="#menu"]{box-shadow:none;padding:0 1em;border-radius:0
}
#header nav a[href="#menu"]:hover,#header nav a[href="#menu"]:active{background-color:inherit
}
}
@media screen and (max-width: 640px){
#header nav a[href="#menu"]{width:4em;white-space:nowrap;text-indent:4em;position:relative
}
#header nav a[href="#menu"]:before{width:inherit;position:absolute;top:0;left:0;text-indent:0;text-align:right;margin-left:0;padding-right:1.25em
}
}
#page-wrapper{transition:filter 0.25s ease
}
#menu{-moz-align-items:center;-ms-align-items:center;-ms-flex-align:center;align-items:center;display:-moz-flex;display:-ms-flex;display:-ms-flexbox;display:flex;-moz-justify-content:center;-ms-justify-content:center;-ms-flex-pack:center;justify-content:center;-moz-pointer-events:none;-webkit-pointer-events:none;-ms-pointer-events:none;pointer-events:none;transition:opacity .35s ease,visibility .35s;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:transparent;background:rgba(46,49,65,0.8);cursor:default;height:100%;left:0;opacity:0;position:fixed;text-align:center;top:0;visibility:hidden;width:100%
}
#menu .inner{padding:2.5em 1.5em .5em 1.5em ;-ms-transform:translateY(0.5em);transform:translateY(0.5em);transition:opacity .35s ease,transform .35s ease;-webkit-overflow-scrolling:touch;background:#4c5c96;border-radius:5px;display:block;max-width:100%;opacity:0;position:relative;width:18em
}
#menu h2{border-bottom:solid 2px rgba(255,255,255,0.125);padding-bottom:1em
}
#menu .close{background-image:url("~@/assets/images/close.svg");background-position:75% 25%;background-repeat:no-repeat;background-size:2em 2em;border:0;content:'';display:block;height:4em;overflow:hidden;position:absolute;right:0;text-align:center;text-indent:4em;top:0;width:4em
}
#menu .links{list-style:none;margin-bottom:1.5em;padding:0
}
#menu .links li{padding:0
}
#menu .links li a{border-radius:5px;border:0;display:block;font-family:Raleway,Helvetica,sans-serif;font-size:0.8em;font-weight:200;letter-spacing:.1em;line-height:1.85em;padding:0.75em 0;text-transform:uppercase
}
#menu .links li a:hover{background:#45558d
}
@media screen and (max-width: 736px){
#menu .inner{max-height:100%;overflow-y:auto;overflow-x:hidden
}
#menu .inner .close{background-size:1.5em 1.5em
}
}
